.App {
  text-align: center;
}

.App-header {
  min-height: 100vh;
  font-size: calc(10px + 2vmin);
  color: white;
}

.Interaction-button {
  width: 10%;
  height: 10%;
  cursor: pointer;
}

.Interaction-button {
  transition: all .1s ease-in-out;
}

.Interaction-button:hover {
  transform: scale(1.1);
}

.Audio-Controller {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
}

.Center-content-to-screen {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}